//anv√§nder partials
@use "partials/navigation";
@use "partials/footer";
@use "partials/review";

body {
    background-color: rgb(0, 0, 0);
    overflow-x: hidden;
    margin-left: -1vw;
    font-family: "Poetsen One", sans-serif;

}


#pos1 {
    width: 80vw;
    padding: 2%;
    color: rgb(255, 255, 255);
    background-size: contain;
    height: fit-content;
    position: relative;
    text-align: center;
    margin: auto;
    margin-top: 5vh;
    margin-bottom: 2vh;
    border-radius: 10px;


    ul {
        list-style: none;
        display: inline-block;
        margin: auto;
    }

    li {
        display: block;
        position: relative;
        margin-right: 1vw;
        width: 50vw;
        font-size: 90%;
        text-align: left;
        color: #ffffff;



    }

    h1 {
        color: rgb(255, 255, 255);
        text-decoration: underline;
        text-align: center;

    }

    h2 {
        font-size: 120%;
        margin-right: 3vw;
        text-align: center;

    }

    p {
        font-size: 90%;
        color: #ffffff;

    }
}




#pos2 {
    #grid2x2 {
        display: grid;
        grid-template-columns: 2fr 2fr;
        grid-template-rows: 2fr;
        position: relative;
        width: 100vw;
        margin-top: 8vh;


    }

    .gridItem {
        width: 40vw;
        height: fit-content;
        border: 1px solid rgb(117, 117, 117);
        margin: auto;
        margin-bottom: 10vh;
        background-color: white;
        border-radius: 10px;
    }

    h1 {
        color: rgb(0, 0, 0);

        text-align: center;
        font-size: 150%;
        text-decoration: underline;


    }

    ul {
        list-style: none;
    }







}


#pos3 {
    width: 100vw;
    background-position-x: 90%;

    height: fit-content;
    position: relative;
    background-image: url(bilder/signburg.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    z-index: 1;
    margin-left: 1vw;

    #divMessage {
        background-color: green;
        padding: 2%;
        border-radius: 10px;
        height: fit-content;
        display: none;
    }

    h2 {
        color: white;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        text-align: center;
        font-size: 180%;
        text-decoration: underline;
    }


    #divForm {
        margin-left: 5vw;
        width: 40vw;
        color: white;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

        form {
            display: flex;
            flex-direction: column;
            margin: auto;
            position: relative;
        }

        label {
            font-size: 100%;
            text-align: left;
            position: relative;
            text-align: start;
            //margin-left: 2vw;
            margin-top: 2vh;
        }

        input {
            margin: auto;
            margin-bottom: 1vh;
            width: 40vw;
            height: 3vh;
        }

        button {
            width: fit-content;
            position: relative;
            height: 5vh;
            font-weight: 900;
            background: none;
            
            border-radius: 10px;
            color: white;
            text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
            margin-bottom: 1vh;
            
        }

        button:hover {
            color: white;
            text-decoration: underline;
            background-color: #f4c885;
        }
    }


    #gridItem2 {
        display: block;
        background-color: white;
        width: fit-content;
        position: relative;


    }


}


#pos4 {
    width: 100vw;
    margin: auto;
    position: relative;
    height: fit-content;
    margin-top: 1vh;
    
    text-align: center;
    margin-top: 5vh;

    iframe {
        width: 100vw;
        margin-left: 1vw;
        margin-top: -1vh;
    }

}



#pos5 {
    width: 100vw;
    background-position: right;
    
    height: fit-content;
    position: relative;
    
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
    margin-left: 1vw;
    margin-top: 5vh;


    #grid2x1 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        position: relative;

    }

    #gridItem1 {
        color: white;
        margin: auto;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

        a {
            color: white;
        }

        a:hover {
            color: rgb(244, 200, 133);
        }
    }

    #gridItem2 {
        margin: auto;
        color: white;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

        form {
            display: flex;
            flex-direction: column;
            margin: auto;
            position: relative;
        }

        label {
            font-size: 130%;
            text-align: left;
            position: relative;
        }

        input {
            margin: auto;
            margin-bottom: 1vh;
            width: 35vw;
            font-size: 140%;

        }

        textarea {
            height: 5vh;
        }

        button {
            width: fit-content;
            position: relative;

            font-weight: 900;
            background-color: #000;
            border-radius: 10px;
            color: white;
            text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
            margin-bottom: 1vh;
            margin-top: 1vh;
        }

        button:hover {
            color: white;
            text-decoration: underline;
            background-color: rgb(244, 200, 133);
        }
    }
}




#pos6 {
    width: 100vw;
    margin: auto;
    position: relative;
    color: white;
    height: fit-content;
    margin-top: 5vh;
    display: block;
    position: relative;
    margin-bottom: 5vh;


    #reviewsDiv {
        margin: auto;
        width: 80vw;
        position: relative;
        background-color: rgb(0, 0, 0);
        text-align: center;


    }

    ul {
       
        width: fit-content;
        text-align: left;
        list-style: none;
        display: inline-flex;
        flex-direction: column;
        border-radius: 10px;
        margin-left: 5vh;
        padding: 5%;



        li {
            display: block;
            width: 20vw;
            margin: auto;
            padding: 2%;
            margin-left: -2vw;
            text-align: left;
            font-size: 130%;
        }
    }

}

