@media (max-width:799px) {

    h1 {
        font-size: 100%;
    }


    #pos1 {
        li {
            width: 70vw;
            margin: auto;
        }
    }




    #pos2 {
        #grid2x2 {
            display: block;
        }

        .gridItem {
            width: 70vw;
        }

        h1 {
            color: rgb(0, 0, 0);

            text-align: center;
            font-size: 150%;
            text-decoration: underline;
            

        }

        h2 {
            font-size: 90%;
        }

        ul {
            list-style: none;
        }

        li {
            font-size: 100%;
        }

    }



    #pos3 {
        width: 100vw;
        background-position-x: 90%;
        height: fit-content;
        position: relative;
        background-image: url(bilder/signburg.jpg);
        background-repeat: no-repeat;
        background-size: contain;
        position: relative;
        z-index: 1;
        margin-left: 1vw;

        #divMessage {
            background-color: green;
            padding: 2%;
            border-radius: 10px;
            height: fit-content;
            display: none;
        }

        h2 {
            color: white;
            text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
            text-align: center;
            font-size: 150%;
            text-decoration: underline;
        }


        #divForm {

            width: 70vw;
            color: white;
            text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

            label {
                font-size: 100%;
                text-align: left;
                position: relative;
                text-align: start;
                margin-top: 2vh;
                margin-left: 5vw;

            }

            input {
                margin: auto;
                margin-bottom: 1vh;
                width: 60vw;
                height: 3vh;
            }

            button {
                margin-left: 5vw;
                margin-top: 1vh;
            }




        }


    }



    #pos5 {
        width: 100vw;
        height: fit-content;
        position: relative;
        position: relative;
        z-index: 1;
        margin-left: 1vw;
        margin-top: 5vh;

        h2 {
            font-size: 120%;
        }


        #grid2x1 {
            display: block;
            position: relative;
            margin: auto;
            width: fit-content;
        }

        #gridItem1 {
            border: 1px solid white;
            border-radius: 10px;
            padding: 5%;
            display: block;
            color: white;
            margin: auto;
            margin-top: 5vh;
            text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

            a {
                color: white;
                font-size: 100%;
            }

            a:hover {
                color: rgb(244, 200, 133);
            }
        }

        #gridItem2 {
            display: block;
            margin-top: 5vh;

            form {
                display: flex;
                flex-direction: column;
                margin: auto;
                position: relative;
            }

            label {
                font-size: 110%;
                text-align: left;
                position: relative;
            }

            input {
                font-size: 120%;
                margin-left: 0vw;

            }


        }
    }


    #pos6 {
        width: 100vw;
        margin: auto;
        position: relative;
        color: white;
        height: fit-content;
        margin-top: 5vh;
        display: block;
        position: relative;
        margin-bottom: 5vh;


        #reviewsDiv {
            margin: auto;
            width: 80vw;
            position: relative;
            background-color: rgb(0, 0, 0);
            text-align: center;


        }

        ul {
            
            width: fit-content;
            text-align: left;
            list-style: none;
            display: inline-flex;
            flex-direction: column;
            border-radius: 10px;
            padding: 5%;
            margin-left: 0vw;



            li {
                display: block;
                width: 45vw;
                padding: 2%;
                margin-left: -2vw;
                text-align: left;
                font-size: 100%;
            }
        }

    }


}